import React from 'react'
import marked from "marked";
import data from "../../api/data";
import Section from '../../components/Section';
import CVTable from '../../components/CVTable';
import { markedInline } from '../../utils/helpers';

function Item(skill) {
  const name = (<span className="skill-name">{skill.name}</span>);
  const content = (<div className="skill-content">
    {Array.isArray(skill.description)
      ? skill.description.map((c, i) => (<div key={i} dangerouslySetInnerHTML={{__html: marked(c)}}/>))
      : (<div dangerouslySetInnerHTML={{__html: marked(skill.description)}}/>)
    }
  </div>);
  return {name, content};
}

function Skills(props) {
  const {title, skills} = props;
  return (
    <Section id="skills">
      <h2 dangerouslySetInnerHTML={{__html: markedInline(title)}}></h2>
      {skills && (
        <CVTable className="skills" items={skills.map(Item)} />
      )}
        
    </Section>
  );
}


export default function DefaultSkills(props) {
  const skills = data.skills || [];
  return <Skills title='Skills' skills={skills} {...props}/>;
}